<template>
  <div class="read">
    <div class="top">
      <span class="fl bg" @click="back()"></span>
      <span class="fl tit">
        <van-pagination
          v-model="currentPage"
          :page-count="allart"
          :show-page-size="currentPage"
          mode="simple"
          prev-text="上一章"
          next-text="下一章"
          @change="togglepage()"
        />
      </span>
      <span class="fl set"></span>
    </div>
    <div class="content" @click="allshow()" v-for="(item,index) in bookslist" :key="index">
      <span>{{item.num}}</span>
      <div v-for="(item1,index1) in item.url" :key="index1">
        <p>{{item1}}</p>
      </div>
    </div>
    <div class="listleft">
      <ul>
        <li
          v-for="(item2,index2) in alllists"
          :key="index2"
          class="onetext"
          @click="artnum(index2)"
        >{{item2.num}}</li>
      </ul>
    </div>
    <!-- 过度效果 -->
    <transition name>
      <van-tabbar class="bot" v-model="active" active-color="#7d7e80">
        <van-tabbar-item icon="ellipsis" @click="togglelist()">目录</van-tabbar-item>
        <van-tabbar-item icon="eye-o" @click="toggleday()">黑白</van-tabbar-item>
        <van-tabbar-item icon="edit" @click="togglesize()">字体</van-tabbar-item>
        <van-tabbar-item icon="expand-o" @click="bigscreen()">屏幕</van-tabbar-item>
      </van-tabbar>
    </transition>
  </div>
</template>

<script>
export default {
  name: "read",
  data() {
    return {
      //总篇章
      allart: 1,
      //改字体
      size: 0,
      //黑白天
      night: 0,
      //目录显示隐藏
      list: false,
      //获取总目录
      alllists: "",
      play: true,
      //获取数据
      currentPage: 0,
      artcurrentPage: 0,
      bookslist: [],
      getbooklist: [],
      active: 0,

      //获取文章标题，用于书架更新内容
      pname: ""
    };
  },
  methods: {
    back() {
      
       //更新收藏的书库
      let bok = JSON.parse(localStorage.getItem("bookslists"));
      // console.log("bok==>", bok);
      // console.log(this.$route.query)
      let bkname = this.$route.query.datname; //获取该书名
      let auter = this.$route.query.dasauthor; //获取作者名

      // console.log("this.pname==>", this.pname);
      for (let i = 0; i < bok.length; i++) {
        if (bok[i].chapterbokname == bkname && bok[i].chapteraut == auter) {
          if(this.pname !== ''){
            bok[i].chapternum = this.pname;
          //将bok的数据上传到bookslists里面
          localStorage.setItem('bookslists',JSON.stringify(bok))
          // console.log("bok[i].chapternum==>", bok[i].chapternum);
          }
        }
      }

      //返回上一级
      this.$router.go(-1);
      
    },
    //点击设置目录显示
    togglelist() {
      if (this.list == false) {
        document.getElementsByClassName("listleft")[0].style.display = "block";
        document.getElementsByClassName("listleft")[0].style.width = "0px";
        document.getElementsByClassName("listleft")[0].style.transition =
          "width 0.5s";
        this.list = true;
      } else {
        this.list = false;
        document.getElementsByClassName("listleft")[0].style.width = "120px";
        document.getElementsByClassName("listleft")[0].style.transition =
          "width 0.5s";
      }
    },
    // 屏幕放大
    bigscreen() {
      document.getElementsByClassName("content")[0].style.bottom = "0px";
      document.getElementsByClassName("content")[0].style.top = "0px";
      document.getElementsByClassName("top")[0].style.display = "none";
      document.getElementsByClassName("bot")[0].style.bottom = "-1.3rem";
    },
    //屏幕显示隐藏的功能框
    allshow() {
      document.getElementsByClassName("content")[0].style.bottom = "50px";
      document.getElementsByClassName("content")[0].style.top = "40px";
      document.getElementsByClassName("top")[0].style.display = "block";
      document.getElementsByClassName("bot")[0].style.bottom = "0";

      //如果目录显示就隐藏目录
      document.getElementsByClassName("listleft")[0].style.width = "0px";
      document.getElementsByClassName("listleft")[0].style.transition =
        "width 0.5s";
    },
    //改字体
    togglesize() {
      if (this.size == 0) {
        document.getElementsByClassName("content")[0].style.fontSize = "18px";
        this.size = 1;
      } else {
        document.getElementsByClassName("content")[0].style.fontSize = "14px";
        this.size = 0;
      }
    },
    //黑夜白天
    toggleday() {
      if (this.night == 0) {
        document.getElementsByClassName("read")[0].style.backgroundColor =
          "#666";
        document.getElementsByClassName("read")[0].style.color = "#f5f5f5";
        this.night = 1;
      } else {
        document.getElementsByClassName("read")[0].style.backgroundColor =
          "#c1c1c1";
        document.getElementsByClassName("read")[0].style.color = "#000";
        this.night = 0;
      }
    },
    // 点击上/下一章切换内容
    togglepage() {
      let self = this;

      let havelist = [];

      let arr = self.$route.query;
      // console.log("arr==>", arr);

      //获取本地缓存数据进行判断获取对应的书籍
      let getbook = JSON.parse(localStorage.getItem("lists"));
      // console.log("getbook==>", getbook);

      // console.log("this.currentPage==>", self.currentPage);

      for (let i = 0; i < getbook.length; i++) {
        //   console.log("getbook[i]", getbook[i]);
        if (
          getbook[i].datas.data.author == arr.dasauthor &&
          getbook[i].datas.data.name == arr.datname
        ) {
          havelist = getbook[i].datas.list;
          this.alllists.push(havelist);
          //每次取1条数据
          let bookspli = havelist.splice(self.currentPage - 1, 1);
          //  console.log('bookspli==>',bookspli)

          for (let j = 0; j < bookspli.length; j++) {
            self.axios
              .get("http://api.pingcc.cn/?xsurl2=" + bookspli[j].url)
              .then(result => {
                //获得转译url的全部数据 result
                // console.log("result ==> ", result);
                bookspli[j].url = result.data.content;

                self.bookslist = bookspli;
                // console.log("this.bookslist==>", self.bookslist);
                this.pname = self.bookslist[0].num;
              })
              .catch(err => {
                // console.log("err ==> ", err);
              });
          }
        }
      }

      //更新收藏的书库
      let bok = JSON.parse(localStorage.getItem("bookslists"));
      // console.log("bok==>", bok);
      // console.log(this.$route.query)
      let bkname = this.$route.query.datname; //获取该书名
      let auter = this.$route.query.dasauthor; //获取作者名

      // console.log("this.pname==>", this.pname);
      for (let i = 0; i < bok.length; i++) {
        if (bok[i].chapterbokname == bkname && bok[i].chapteraut == auter) {
          bok[i].chapternum = this.pname;
          //将bok的数据上传到bookslists里面
          localStorage.setItem('bookslists',JSON.stringify(bok))
          // console.log("bok[i].chapternum==>", bok[i].chapternum);
        }
      }

    },
    //点击目录章节切换内容
    artnum(numindex) {
      this.currentPage = numindex + 1;
      // console.log(this.currentPage)

      let self = this;

      let havelist = [];

      let arr = self.$route.query;
      // console.log("arr==>", arr);

      //获取本地缓存数据进行判断获取对应的书籍
      let getbook = JSON.parse(localStorage.getItem("lists"));
      // console.log("getbook==>", getbook);

      // console.log("this.currentPage==>", self.currentPage);

      for (let i = 0; i < getbook.length; i++) {
        //   console.log("getbook[i]", getbook[i]);
        if (
          getbook[i].datas.data.author == arr.dasauthor &&
          getbook[i].datas.data.name == arr.datname
        ) {
          havelist = getbook[i].datas.list;
          this.alllists.push(havelist);
          //每次取1条数据
          let bookspli = havelist.splice(self.currentPage - 1, 1);
          // console.log("bookspli==>", bookspli);

          //用于更改书架阅读到第几章
          this.pname = bookspli[0].num;

          for (let j = 0; j < bookspli.length; j++) {
            self.axios
              .get("http://api.pingcc.cn/?xsurl2=" + bookspli[j].url)
              .then(result => {
                //获得转译url的全部数据 result
                // console.log("result ==> ", result);
                bookspli[j].url = result.data.content;

                self.bookslist = bookspli;
                // console.log("this.bookslist==>", self.bookslist);
              })
              .catch(err => {
                // console.log("err ==> ", err);
              });
          }
        }
      }

      //更新收藏的书库
      let bok = JSON.parse(localStorage.getItem("bookslists"));
      // console.log("bok==>", bok);
      // console.log(this.$route.query)
      let bkname = this.$route.query.datname; //获取该书名
      let auter = this.$route.query.dasauthor; //获取作者名

      // console.log("this.pname==>", this.pname);
      for (let i = 0; i < bok.length; i++) {
        if (bok[i].chapterbokname == bkname && bok[i].chapteraut == auter) {
          bok[i].chapternum = this.pname;
          //将bok的数据上传到bookslists里面
          localStorage.setItem('bookslists',JSON.stringify(bok))
          // console.log("bok[i].chapternum==>", bok[i].chapternum);
        }
      }

    }
  },

  created() {
    let self = this;

    let havelist = [];

    let arr = self.$route.query;
    // console.log("arr==>", arr);

    //获取本地缓存数据进行判断获取对应的书籍
    let getbook = JSON.parse(localStorage.getItem("lists"));
    // console.log("getbook==>", getbook);

    // console.log("this.currentPage==>", self.currentPage);

    for (let i = 0; i < getbook.length; i++) {
      //   console.log("getbook[i]", getbook[i]);
      if (
        getbook[i].datas.data.author == arr.dasauthor &&
        getbook[i].datas.data.name == arr.datname
      ) {
        havelist = getbook[i].datas.list;
        //  console.log('havelist==>',havelist)
        this.allart = havelist.length - 1;

        //每次取1条数据
        let bookspli = havelist.splice(self.currentPage, 1);
        //  console.log('bookspli==>',bookspli)

        for (let j = 0; j < bookspli.length; j++) {
          self.axios
            .get("http://api.pingcc.cn/?xsurl2=" + bookspli[j].url)
            .then(result => {
              //获得转译url的全部数据 result
              // console.log("result ==> ", result);
              bookspli[j].url = result.data.content;

              self.bookslist = bookspli;
              // console.log("this.bookslist==>", self.bookslist);
            })
            .catch(err => {
              // console.log("err ==> ", err);
            });
        }
      }
    }

    let newhavelist = [];

    //获取本地缓存数据进行判断获取对应的书籍
    let getbooks = JSON.parse(localStorage.getItem("lists"));
    // console.log("getbook==>", getbook);

    for (let i = 0; i < getbooks.length; i++) {
      //   console.log("getbooks[i]", getbooks[i]);
      if (
        getbooks[i].datas.data.author == arr.dasauthor &&
        getbooks[i].datas.data.name == arr.datname
      ) {
        newhavelist = getbooks[i].datas.list;
        this.alllists = newhavelist;
      }
    }
    // console.log("this.alllists==>",this.alllists)
  }
};
</script>

<style lang="less">
.van-pagination__item {
  border-radius: 25px;
  background-color: #969696;
  color: #3c3c3c;
}
.van-pagination__item:active {
  background-color: #939698;
  color: #d4d2d2;
}
[class*="van-hairline"]::after {
  border: none;
}
</style>
<style lang="less" scoped>
//激活设置按钮
.listbox-leave-active,
.listbox-enter-active {
  transition: all 5s ease;
}
.listbox-enter,
.listbox-leave-active {
  left: 100px;
}
.listbox-leave {
  left: 100px;
}

.van-tabbar {
  background-color: #b4b4b4;
}
.van-tabbar-item--active:active {
  color: #000 !important;
}
.read {
  height: 100%;
  background-color: #c1c1c1;
  .top {
    height: 40px;
    background: #b4b4b4;
    .bg {
      width: 15px;
      height: 30px;
      background: url("../assets/back.png") no-repeat center center;
      background-size: 20px;
      margin: 5px 6px 0;
    }
    .tit {
      width: calc(100% - 27px - 45px - 20px);
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-size: 16px;
      padding: 0 10px;
    }
    .set {
      width: 25px;
      height: 25px;
      background: url("../assets/set.png") no-repeat center center;
      background-size: 22px;
      margin: 7.5px 10px;
    }
  }
  .content {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 50px;
    overflow-y: auto;
    font-size: 14px;
    line-height: 25px;
    padding: 8px;
    > span {
      display: block;
      text-align: center;
    }
    div {
      > p {
        margin: 5px 0;
        text-indent: 12px;
      }
    }
  }
  .listleft {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 50px;
    width: 120px;
    background: #a5a5a5;
    overflow-y: auto;
    display: none;
    padding-top: 5px;
    ul {
      padding: 0 10px;
      overflow-y: auto;
      > li {
        text-align: left;
        height: 20px;
        line-height: 20px;
        padding: 5px 0 5px;
        border-bottom: 1px solid #333;
        &:hover {
          background-color: #666;
        }
      }
    }
  }
}
</style>